$theme-100: #4f68f1;
$theme-200: #536dfe22;
$text-100: #000000;
$text-300: #585b63;
$text-400: #fcfbfb;
$surface-900: #f7f9fc;
$shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;

.projects-cards {
 display: grid;
 grid-template-columns: 1fr;
 gap: .875em;
 inline-size: 100%;
 margin-block: 3em;

 .card {
  display: flex;
  border-radius: .5em;
  position: relative;
  box-shadow: $shadow;
  overflow: hidden;

  &:hover {
   .card-image {
    transform: scale(1.15);
   }
  }

  .card-image {
   display: flex;
   align-items: center;
   position: relative;
   block-size: 100%;
   inline-size: 100%;
   overflow: hidden;
   border-radius: inherit;
   transition: all 200ms ease;

   &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    inline-size: 100%;
    block-size: 100%;
    background: linear-gradient(rgba(4,9,30,.7),rgba(4,9,30,.65));
   }

   img {
    display: block;
    object-fit: cover;
    block-size: 100%;
    inline-size: 100%;
    border-radius: inherit;
   }
  }

  .card-details {
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: .5em;
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   inline-size: 100%;
   z-index: 10;

   h4 {
    font-size: 1.25em;
    font-weight: 500;
    letter-spacing: .02em;
    color: $text-400;
    text-shadow: 0 1px 0 $text-100;
   }

   svg {
    fill: $text-400;
   }
  }
 }

 @media screen and (min-width: 600px) {
  grid-template-columns: repeat(2, 1fr);
 }

 @media screen and (min-width: 992px) {
  grid-template-columns: repeat(3, 1fr);
 }
}