$surface-900: #f7f9fc;
$shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;

.artboards-cards {
 display: grid;
 grid-template-columns: 1fr;
 gap: .875em;
 margin-block: 3em;

 .card {
  display: flex;
  border-radius: .5em;
  position: relative;
  background: $surface-900;
  box-shadow: $shadow;

  .card-image {
   display: flex;
   inline-size: 100%;
   block-size: 100%;
   border-radius: inherit;
   overflow: hidden;

   img {
    display: block;
    object-fit: cover;
    inline-size: 100%;
    block-size: 100%;
    border-radius: inherit;
   }
  }
 }

 @media screen and (min-width: 600px) {
  grid-template-columns: repeat(2, 1fr);
 }
}