$theme-100: #4f68f1;
$text-200: #3c4043;
$text-300: #585b63;
$scrim-100: #00000098;
$surface-800: #ffffff;
$surface-900: #f7f9fc;
$shadow-100: 0 0 #0000,0 0 #0000,0 1px 3px 0 rgba(0,0,0,.1),0 1px 2px -1px rgba(0,0,0,.1);

nav {
 display: block;
 inline-size: 100%;
 padding-block: .75em;
 position: fixed;
 top: 0;
 left: 0;
 z-index: 1000;
 background: $surface-900;
 transition: box-shadow .0875s linear;

 &.shadow {
  box-shadow: $shadow-100;
 }

 .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  inline-size: 100%;
  max-inline-size: 1200px;
  margin-inline: auto;
  padding-inline: .75em;

  & > a {
   display: inline-flex;
   align-items: center;
   gap: .25rem;
   font-size: .825em;
   color: $text-200;
   font-family: "Plus Jakarta Sans", system-ui, sans-serif;
   position: relative;
   text-transform: uppercase;
   letter-spacing: .03em;

   strong {
    font-weight: 600;
    color: $theme-100;
   }
  }

  button {
   display: inline-flex;
   align-items: center;
   justify-content: center;

   @media screen and (min-width: 768px) {
    display: none;
   }
  }

  ul {
   display: none;

   li {
    display: inline-flex;
    align-items: center;
    justify-content: center;

    a {
     display: inline-flex;
     align-items: center;
     justify-content: center;
     text-align: center;
     font-family: "Plus Jakarta Sans", system-ui, sans-serif;
     font-weight: 450;
     color: $text-200;
     font-size: .825em;
     position: relative;
     text-transform: uppercase;
     letter-spacing: .01em;
     padding: .5em .375em;

     &.active {
      color: $theme-100;
     }
    }
   }

   @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
    gap: .5em;
   }
  }
 }

 aside {
  position: fixed;
  top: 0;
  left: -260px;
  inline-size: 260px;
  block-size: 100vh;
  overflow: hidden;
  transition: all 200ms ease 0s;
  z-index: 1000;
  box-shadow: $shadow-100;

  .scrim {
   position: fixed;
   top: 0;
   left: 0;
   inline-size: 100%;
   block-size: 100%;
   overscroll-behavior: contain;
   pointer-events: inherit;
   visibility: hidden;
   z-index: 1000;
   background: $scrim-100;
   -webkit-tap-highlight-color: transparent;

   &::after {
    content: "";
    display: block;
    block-size: 200%;
    inline-size: 200%;
   }
  }

  .drawer {
   display: flex;
   flex-direction: column;
   align-items: center;
   position: relative;
   block-size: 100%;
   inline-size: 100%;
   padding-block: 1em;
   z-index: 1001;
   background: $surface-800;

   .drawer-header {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-block-end: auto;

    a {
     display: inline-flex;
     align-items: center;
     gap: .25rem;
     font-size: 1em;
     color: $text-200;
     font-family: "Plus Jakarta Sans", system-ui, sans-serif;
     position: relative;
     text-transform: uppercase;
     letter-spacing: .03em;
  
     strong {
      font-weight: 600;
      color: $theme-100;
     }
    }
   }

   .drawer-figure {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-block-end: auto;

    img {
     display: block;
     object-fit: cover;
     inline-size: 140px;
     block-size: 140px;
     border-radius: 50%;
    }
   }

   .drawer-navlinks {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-block-end: auto;

    ul {
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
     gap: 1em;
     inline-size: 100%;

     li {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      inline-size: 100%;

      a {
       display: inline-flex;
       align-items: center;
       justify-content: center;
       text-align: center;
       inline-size: 100%;
       font-family: "Plus Jakarta Sans", system-ui, sans-serif;
       font-weight: 450;
       color: $text-200;
       font-size: 1.125em;
       position: relative;
       text-transform: uppercase;
       letter-spacing: .01em;
       padding: .75em .375em;

       &.active {
        color: $theme-100;
       }
      }
     }
    }
   }

   .drawer-footer {
    display: flex;
    align-items: center;
    justify-content: center;
    inline-size: 100%;
    margin-block-end: 4em;

    ul {
     display: flex;
     align-items: center;
     justify-content: space-evenly;
     inline-size: 100%;

     li {
      display: inline-flex;
      align-items: center;
      justify-content: center;

      a {
       display: inline-flex;
       align-items: center;
       justify-content: center;

       svg {
        block-size: 1.25em;
        inline-size: 1.25em;
        fill: $text-300;
       }
      }
     }
    }
   }
  }

  &.expanded {
   left: 0px;

   .scrim {
    visibility: visible;
   }
  }

  @media screen and (min-width: 768px) {
   display: none;
  }
 }
}